/*************************
Profile Update Page Styling
*************************/
.profile--update--section {
  background-image: linear-gradient(to right, #a34be7, #a745e8, #ac3ee8, #b036e9, #b52ce9, #b32dea, #b22eec, #b02fed, #a73af0, #9d42f2, #944af4, #8a50f6);
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profile--update--section h1 {
  color: var(--color-white);
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 2rem;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  letter-spacing: 0.75px;
}
.profile--update--section > form {
  padding: 1rem;
  background-color: var(--color-white);
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.4);
  width: 90%;
  max-width: 720px;
  margin: 0 auto 2rem;
  color: var(--input-label-color);
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.35rem;
}

.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-flow: column wrap;
}
.form__group label {
  font-size: 0.95rem;
  font-weight: 600;
  line-height: 1.2rem;
}
.form__group input {
  font-family: var(--primary-font);
  font-size: 0.95rem;
  width: 100%;
  padding: 0.45rem 0.5rem;
  border: 1px solid var(--input-field-border);
  border-radius: 2px;
  outline: none;
  border-radius: 3px;
}
.form__group input:focus {
  outline: 1px solid var(--color-black);
}
.form__submit__group input {
  margin-top: 0.5rem;
  padding: 0.75rem 1.5rem;
}

/************************
Avatar related Form & its container styling
*************************/
.dropArea {
  padding: 0.75rem;
  background-color: var(--color-white);
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.4);
  width: 90%;
  max-width: 720px;
  margin: 0 auto 2rem;
}
.dropArea h3 {
  margin-bottom: 0.35rem;
  text-align: center;
  font-size: 1.1rem;
}
.dropArea form {
  border: 2px dashed #999;
  padding: 1.5rem 0.5rem;
  text-align: center;
  font-family: var(--tertiary-font);
}
.dropArea form.highlight {
  border-color: #222;
}
.dropArea form div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
}
.dropArea form label {
  margin-bottom: 0.5rem;
  font-family: var(--primary-font);
}
.dropArea form span {
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-weight: 600;
}
.dropArea form img {
  width: 90%;
  max-width: 20rem;
  margin: 1rem auto 0;
  display: block;
  border: 1px solid #777;
  border-radius: 3px;
}

/***********************
Styling File Field
************************/
input[type=file] {
  font-family: var(--primary-font);
  font-size: 1rem;
  font-weight: 500;
}
input[type=file]::file-selector-button {
  padding: 0.75rem 1rem;
  font-family: inherit;
  font-size: 0.9rem;
  text-transform: uppercase;
  background-color: var(--btn-color);
  border: none;
  border-radius: 4px;
  font-weight: 400;
  color: var(--color-white);
  letter-spacing: 0.5px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
input[type=file]::file-selector-button:hover, input[type=file]::file-selector-button:focus {
  cursor: pointer;
  opacity: 0.85;
}
input[type=file]::-webkit-file-upload-button {
  padding: 0.75rem 1rem;
  font-family: inherit;
  font-size: 0.9rem;
  text-transform: uppercase;
  background-color: var(--btn-color);
  border: none;
  border-radius: 4px;
  font-weight: 400;
  color: var(--color-white);
  letter-spacing: 0.5px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
input[type=file]::-webkit-file-upload-button:hover, input[type=file]::-webkit-file-upload-button:focus {
  cursor: pointer;
  opacity: 0.85;
}
input[type=file]::-ms-browse {
  padding: 0.75rem 1rem;
  font-family: inherit;
  font-size: 0.9rem;
  text-transform: uppercase;
  background-color: var(--btn-color);
  border: none;
  border-radius: 4px;
  font-weight: 400;
  color: var(--color-white);
  letter-spacing: 0.5px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
input[type=file]::-ms-browse:hover, input[type=file]::-ms-browse:focus {
  cursor: pointer;
  opacity: 0.85;
}

/*************************
Error message styling
*************************/
.error__msg {
  font-size: 0.8rem;
  color: var(--error-msg-color);
  font-weight: 600;
  font-family: var(--tertiary-font);
  display: block;
}